let _n = 1;

cc.Class({
    extends: cc.Component,
    properties: {

    },

    callback: function (event, customEventData) {
        // 这里 event 是一个 Event 对象，你可以通过 event.target 取到事件的发送节点
        let scene = cc.director._scene;

        _n = -_n;
        for (var i = 1; i <= 16; i++) {
            let ri = scene.getChildByName("sprite" + i);

             ri.stopAllActions();
            if (_n > 0) {
                // let p = ri.getPosition()
                // p.x = 100;
                // ri.setPosition(p);
                // var action = cc.moveBy(1, cc.v2(200, p.y));
                ri.runAction(cc.repeatForever(cc.rotateBy(1.0, _n * 360)));
            }
        }
        
    },

    callback1: function (event, customEventData) {
        // 这里 event 是一个 Event 对象，你可以通过 event.target 取到事件的发送节点
        let scene = cc.director._scene;
        let ri = scene.getChildByName("heads");
        if (ri != null) {
            ri.removeFromParent();
        }
        else {
            cc.loader.loadRes('heads', function (err, prefab) {
                if (err) {
                    cc.error(err.message || err);
                    return;
                }
                cc.log('Result should be a prefab: ' + (prefab instanceof cc.Prefab));
                var heads = cc.instantiate(prefab);
                heads.name = "heads";
                scene.addChild(heads);
            });
        }
        
    },

    start: function () {
        // let scene = cc.director._scene;
        // for (var i = 1; i <= 16; i++) {
        //     let ri = scene.getChildByName("sprite" + i);

        //     ri.stopAllActions();
        //     // let p = ri.getPosition()
        //     // p.x = 100;
        //     // ri.setPosition(p);
        //     // var action = cc.moveBy(1, cc.v2(200, p.y));
        //     ri.runAction(cc.repeatForever(cc.rotateBy(1.0, _n*360)));
        // }
    },
});

